"use client";
import { useState } from "react";

export default function AddComments({ users, onAddComment }: any) {
  const [selectedUser, setSelectedUser] = useState("");
  const [text, setText] = useState("");

  const handleSubmit = (e: any) => {
    e.preventDefault();
    if (!selectedUser || !text) return;
    onAddComment(Number(selectedUser), text);
    setText("");
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      {/* User Dropdown */}
      <select
        className="w-full border rounded p-2"
        value={selectedUser}
        onChange={(e) => setSelectedUser(e.target.value)}
      >
        <option value="">Select a user</option>
        {users.map((u: any) => (
          <option key={u.id} value={u.id}>
            {u.name} ({u.company.name})
          </option>
        ))}
      </select>

      {/* Comment Box */}
      <textarea
        className="w-full border rounded p-2"
        placeholder="Write your comment..."
        value={text}
        onChange={(e) => setText(e.target.value)}
      />

      <button
  type="submit"
  className="flex items-center gap-2 bg-blue-600 hover:bg-blue-700 text-white font-medium px-4 py-2 rounded-md transition-colors"
>
  <Send size={16} className="stroke-current" />
  Post Comment
</button>
    </form>
  );
}
