
"use client";

type CommentCardProps = {
  initials: string;
  name: string;
  email: string;
  company: string;
  comment: string;
};

export default function CommentCard({
  initials,
  name,
  email,
  company,
  comment,
}: CommentCardProps) {
  return (
    <div className="rounded-lg border text-card-foreground shadow-sm p-6 bg-comment-bg border-comment-border hover:bg-comment-hover transition-all duration-200 hover:shadow-lg">
      <div className="flex gap-4">
      
        <div className="flex-shrink-0">
          <div className="w-12 h-12 bg-avatar-bg text-avatar-text rounded-full flex items-center justify-center font-semibold text-lg cursor-pointer hover:ring-2 hover:ring-primary hover:ring-offset-2 transition-all duration-200">
            {initials}
          </div>
        </div>

   
        <div className="flex-1 space-y-2">
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-1">
            <div>
              <h3 className="font-semibold text-foreground text-lg cursor-pointer hover:text-primary transition-colors duration-200">
                {name}
              </h3>
              <p className="text-sm text-muted-foreground">{email}</p>
            </div>
            <div className="text-sm text-muted-foreground font-medium">
              {company}
            </div>
          </div>

          <div className="pt-2">
            <p className="text-foreground leading-relaxed">{comment}</p>
          </div>
        </div>
      </div>
    </div>
  );
}
